import React from 'react'
import {Label, Input,  } from 'reactstrap';
import '../Form-Field/FormField.css';


 const FormField=({PlaceHolder,label,Type,OptionData})=> {
    return( 
        <React.Fragment>
            <Label className="text-white"><h4>{label}</h4></Label>
        <Input className="field-height " type={Type} name={Label}  placeholder={PlaceHolder} >
    {Type==='select' && OptionData ? OptionData.map((option)=><option>{option}</option>) : ''}
        </Input>
        </React.Fragment>
          )
}

export default FormField;